.App {
  text-align: center;
}

html {
  overflow: hidden;
  background-color: #61dafb;
}

a {
  text-decoration: none;
  color: #ffffff;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-link {
  color: #61dafb;
}

.grass-bottom {
  position: absolute;
  bottom: 0;
  left: 0%;
  width: 100vw;
}

.sun {
  position: absolute;
  top: 4vh;
  right: 4vw;
  width: 25vw;
  height: 25vh;
}

.content-wrapper {
  position: absolute;
  padding: 40px;
  display: flex;
  flex-direction: column;
  background-color: rgba(55, 130, 174, 0.225);
  border-radius: 10px;
  left: 50vw;
  top: 40vh;
  transform: translate(-50%, -50%);
  z-index: 2;
  max-width: 30vw;
  color: white;
  font-family: "Roboto", sans-serif;
}

.empty {
  flex: 0.99;
}

header {
  width: 100vw;
  height: 10vh;
  position: absolute;
  top: 0;
  z-index: 2;
  display: flex;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  background-color: #39819522;
}

header > h1 {
  color: white;
  font-size: 2em;
  font-weight: 400;
  font-family: "Roboto", sans-serif;
}

header > input {
  margin: 20px;
  padding: 10px;
  border-radius: 5px;
  border: none;
  font-size: 1.2rem;
  font-family: "Roboto", sans-serif;
}

header > button {
  margin: 20px;
  padding: 10px;
  border-radius: 5px;
  border: none;
  font-size: 1.2rem;
  font-family: "Roboto", sans-serif;
  background-color: #61dafb;
  color: azure;
}

header > button:disabled {
  background-color: #398195;
  color: azure;
  opacity: 0.5;
}

@keyframes cloud-move {
  0% {
    transform: translateX(0);
  }
  50% {
    transform: translate(60vw);
  }
  100% {
    transform: translate(0vw);
  }
}

@keyframes cloud-move-reverse {
  0% {
    transform: translateX(0);
  }
  50% {
    transform: translate(-60vw);
  }
  100% {
    transform: translate(0vw);
  }
}

@keyframes cloud-stationary {
  0% {
  }
  50% {
    transform: translate(0, 1vh);
  }
  100% {
    transform: translate(0, 0vh);
  }
}

.cloudy-bg {
  background-color: rgb(164, 209, 221);
  width: 100vw;
  height: 100vh;
}

.cloudy {
  animation: cloud-stationary 10s ease infinite;
  position: absolute;
  z-index: 1;
  left: 0;
}

.cloudy-moving {
  animation: cloud-move 80s ease infinite;
  position: absolute;
  z-index: 1;
  left: 0;
}

.rainy-bg {
  background-color: rgb(121, 144, 150);
  width: 100vw;
  height: 100vh;
}

.moving {
  animation: cloud-move 80s linear infinite;
}

.moving-reverse {
  animation: cloud-move-reverse 80s linear infinite;
}

.rain-cloud-div {
  position: absolute;
  z-index: 1;
  width: fit-content;
  height: fit-content;
}

.rain {
  position: absolute;
  bottom: 25%;
  left: 10%;
  display: flex;
  width: 75%;
  justify-content: space-evenly;
  z-index: 1;
}

.rain > span {
  position: relative;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: blue;
  animation: rainfall 1s linear infinite;
  animation-duration: calc(15s / var(--i));
  transform-origin: bottom;
}

@keyframes rainfall {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(80vh);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
